.recipe{
    display: flex;
    box-shadow: 0px 5px 20px gray;
    margin: 20px;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    border-radius: 3%
}

.image{
    border-radius:6rem;
    width: 15rem;
}

.calories{
    font-family: monospace;
    font-weight: 700;
    font-size: 1rem;
}

.ingredients{
    padding: .5rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.15rem;
    margin-left:5rem;
    margin-right: 5rem;
}