.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;


}.search-bar{
  width: 50%;
  border:none;
  padding: 10px;
}

.search-button{
  background: lightcoral;
  border:none;
  padding: 10px 20px;
  cursor: pointer;
  color: white;
  text-transform: uppercase;
}

.search-button:hover{
  background: purple;
  color: white;
  border-radius:2rem;
  transform: scale(1.2);

}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

